import { debounce } from 'underscore';
import { Resp } from '../modules/dev/_helpers';
import TimelineMax from 'gsap/TimelineMax';

/**
 * Website's common scripts (example).
 *
 * @module Common
 */

export class Common {
  /**
   * Cache data, make preparations and initialize common scripts.
   */
  constructor() { 

    if (Resp.isDesk) {
      $('body').addClass('with-hover'); 
      $('.header__top__contacts__button').click(function(){
        $('.header').removeClass('fixed active visible');
      });
    } 
    if (!Resp.isDesk) {
      console.log('mobile')      
      $('.mobile-active').addClass('active');
      $('.products').addClass('active');
    }
         

    this.scrollTop = 0;
    // initialize after construction
    this.init();
  }

  throttle = (func, wait) => {
    let context, args, timeout, throttling, more, result;
    const whenDone = debounce(() => { more = throttling = false; }, wait);
    return function() {
      context = this; args = arguments;
      var later = function() {
        timeout = null;
        if (more) func.apply(context, args);
        whenDone();
      };
      if (!timeout) timeout = setTimeout(later, wait);
      if (throttling) {
        more = true;
      } else {
        result = func.apply(context, args);
      }
      whenDone();
      throttling = true;
      return result;
    };
  }; 

  mobileActions() {
    console.log('this is mobile');

    const menuOpenBtn = $('.header__top__burger');
    const menuContainer = $('.header__bottom');
    const menuChildOpen = $('.header__menu__link.has-active'); 

    const toggleMenu = () => {
      menuOpenBtn.toggleClass('active'); 
      menuContainer.toggleClass('active');  
      $('body').toggleClass('hidden');
      $('html').toggleClass('hidden'); 
      if(!menuOpenBtn.hasClass('active')){
        $('.sub__menu').removeClass('active');
        $('.header__menu__link').removeClass('active');
        $('.mobile-active').addClass('active');
        $('.products').addClass('active');
      }
    } 

    menuOpenBtn.click(function(){ 
        toggleMenu();          
    });
Ñ‚
    menuChildOpen.click(function() {
      
      if(!$(this).siblings('.sub__menu').hasClass('active')) {
        $('.header__menu__link').removeClass('active');
        $('.sub__menu').removeClass('active');
        $(this).siblings('.sub__menu').addClass('active');
        $(this).addClass('active');
      } else { 
        $('.sub__menu').removeClass('active');
        $(this).removeClass('active');
      }        
    });

    const phoneBtn = $('.header__top__contacts__button');
    
    phoneBtn.click(function() {
      menuOpenBtn.removeClass('active'); 
      menuContainer.removeClass('active');
      $('.sub__menu').removeClass('active');
      $('.header__menu__link').removeClass('active');
      $('.mobile-active').addClass('active');
      $('.products').addClass('active');
      $(this).siblings('.header__top__contacts__numbers').toggleClass('active');
    });

    const $win = $(window); // or $box parent container
		const $box = $(".header__top__contacts__numbers"); 
        
    $win.on("click.Bst", function(event){		
      if ( ($box.has(event.target).length == 0 && !$box.is(event.target)) &&
           (phoneBtn.has(event.target).length == 0 && !phoneBtn.is(event.target))
      ){
        $box.removeClass('active')
      }
    });
  }

  headerActivate(e) {
    const header = $('.header');
    const headerHeight = 300;
    const currentScrollTop = window.pageYOffset;
    
    if(currentScrollTop > 70)  {
      console.log('do')
      header.addClass('fixed active visible');
    } else {
      header.removeClass('fixed active visible');
    }

    

    $(".header__top__contacts__numbers").removeClass('active');

    // if(currentScrollTop > 200)  {
    //   header.addClass('fixed');
    // } else {
    //   header.removeClass('fixed');
    //   header.removeClass('active');
    // }
      
    // if(currentScrollTop > headerHeight)  {
    //   const scrollTop = window.pageYOffset;
    //   if(!header.hasClass('active'))
    //     header.addClass('active'); 
    //   if (this.scrollTop > currentScrollTop) {
    //     if(!header.hasClass('visible'))
    //       header.addClass('visible'); 
    //   } else {        
    //     if(header.hasClass('visible')) 
    //       header.removeClass('visible');
    //   }    
    //   this.scrollTop = currentScrollTop; 
    // } else if((currentScrollTop < headerHeight) && (header.hasClass('active'))) { 
    //   header.removeClass('visible'); 
    // }
  }
  /**
   * Initialize common scripts.
   */
  init() { 


    $(document).scroll(this.throttle(this.headerActivate, 100));  
    if (Resp.isMobile)
      this.mobileActions();
    
  }
}

/** Export initialized common scripts by default */
export default new Common();



